<template>
	<div class="newWallet">
		<b-alert variant="success" show>{{msg}}</b-alert><br>
		<b-alert show>Location: {{location}}</b-alert>
		<h4>Backend response:{{response}}</h4>
		<button @click="callRestService">Call Api </button>

	</div>
</template>

<script>
	// import axios from 'axios'
	import {AXIOS} from './http-common.js'
	export default {
	  name: 'NewWallet',
	  data () {
	    return {
	      msg: 'Welcome to NewWallet',
	      location: 'D:\\WorkingFolder\\workspace\\monager\\frontend\\src\\components\\NewWallet.vue',
	      response: [],
          errors: []
	    }
	  },
	  methods:{
	  	callRestService(){
	  		AXIOS.get('/hello')
	  		.then(response=>{
	  			this.response=response.data
	  			console.log(response.data)
	  		})
	  		.catch(e=>{
	  			this.errors.push(e)
	  		})
	  		/*
	  		console.log("enter the methods")
	  		this.msg="New Value"
	  		this.response=[{
	  			a:'1',
	  			a:'2',
	  			a:'3',
	  			a:'4'
	  		}]
	  		console.log(this.response)
	  		*/
	  	}	  	
	  }
	}	
</script>


