<template>
  <div id="app">
  <div>
    <b-navbar variant="dark" type="dark" toggleable>
      <b-navbar-brand href="#"><span class="border-bottom border-primary">{{appName}}</span></b-navbar-brand>
      <div class="collapse navbar-collapse navbar-dark bg-dark rounded" id="navbarNav" style="margin-left:60px">
      <ul class="navbar-nav nav-fill">
      <li class="nav-item active" v-for="option in options" v-bind:key="option.text">
                      <a class="nav-link " v-bind:href="option.href">{{option.text}}<span class="sr-only"></span></a>
                    </li>
                </ul>
              </div>
    </b-navbar>
  </div>
  <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: 'App',
  data: function (){
    console.log("reached App Page DOM");
    return {
      appName: 'monager',
      options: [
      {
        href: '/',
        text: 'Home'
      },
      {
        // href: '/#/newWallet', use this method when not using mode: 'history' in index.js is not used
        href: "newWallet",
        text: 'View Shared Wallets'
      },
      {
        href: '/about',
        text: 'Create New Shared Wallet'
      }
      ]
    }
  }
}
</script><!--
<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>
-->



