import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import NewWallet  from '@/components/NewWallet'

const About = { template: '<p>About page</p>' }

Vue.use(Router)

const routes = {
  '/about': About
}

export default new Router({
  mode: 'history',
  routes: [
    {	
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
     path: '/NewWallet',
     name: 'NewWallet',
     component: NewWallet	
    },
    {
    	path:'/about',
    	name: 'About',
    	component: About	
    }
  ]
})



